.About__us__container {
    padding: 20px;
}
.About__us__block1 {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.About__us__block1 p {
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
}
.About__us__block2 {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.About__us__block2 p{
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
}
.About__us__block2 img{
    margin-top: 10px;
}
@media(max-width: 623px) {
    .About__us__block1 img {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    .About__us__block2 img{
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}