.shoe__ul {
  position: absolute;
  top: 175px;
  width: 600px;
  max-height: 300px;
  background-color: #FFFFFF;
  overflow-y: scroll;
}

.shoe__li:hover {
  background-color: grey;
}

.shoe__li {
  padding: 10px;
  cursor: pointer;
  width: 100%;
  margin-top: 5px;
  border-radius: 10px;
}

.input {
  display: flex;
  width: 800px;
  height: 50px;
  border-radius: 10px;
  margin-top: 20px;
  margin-left: 10px;
  border: 1px solid #a2a0a0;
}

.container__bottom {
  display: flex;
  justify-content: space-around;
}

.image__top {
  height: 60px;
  width: 80px;
  margin-top: 5px;
  border-radius: 10px;
  cursor: pointer;
}/*# sourceMappingURL=Style.css.map */